import { onsiteUpload } from '../utils'
import { POP_UP, POPUP_OF, ONSITE_POPUP, MEDIA_POP, SHARE_POPUP, MEDIA_POPOF, SHARE_POPOF, ONSITE_TOKN } from './reducersKeys'
const initialState = { isShowPopUp: false, clickCta: false, data: null, onSiteIsShowPopUp: onsiteUpload ? true : false, reportStatus: false, shareStatus: false, reportData: null, shareData: null, ONSITE_TOKN: null }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case POP_UP: { return { ...state, isShowPopUp: true, data: action.payload } }
        case POPUP_OF: { return { ...state, isShowPopUp: false, data: null } }
        case ONSITE_POPUP: { return { ...state, onSiteIsShowPopUp: action.payload, data: null } }
        case MEDIA_POP: { return { ...state, reportStatus: true, reportData: action.payload } }
        case SHARE_POPUP: { return { ...state, shareStatus: true, shareData: action.payload } }
        case MEDIA_POPOF: { return { ...state, reportStatus: false, reportData: null } }
        case SHARE_POPOF: { return { ...state, shareStatus: false, shareData: null } }
        case ONSITE_TOKN: { return { ...state, ONSITE_TOKN: action.payload } }
        default:
            return { ...state }
    }

}