import { MEDIA_POP, MEDIA_POPOF,  ONSITE_POPUP, POP_UP, POPUP_OF, SHARE_POPOF, SHARE_POPUP, THEME_DATA } from "../reducers/reducersKeys";
import store from "../store";
import { activeTheme } from "../utils";
import { addfeedload, ONSITE_UPLOAD, REPORT_MEDIA, SHARE_POST } from "./api";
import HttpClient from "./http-client"; 
export let dataSource;
let messageSent = false;
export let dataRegion;

export const reportMedia = (data) => 
    new HttpClient().post(REPORT_MEDIA, data);

const dispatchAction = (type, payload) => (dispatch) => dispatch({ type, payload });
export const reportMediaClosePopUp = (data) => dispatchAction(MEDIA_POPOF, data);
export const onsiteTokenUpdate = (ONSITE_TOKN) => dispatchAction(ONSITE_TOKN, ONSITE_TOKN);
export const closePopUP = (data) => dispatchAction(POPUP_OF, data);
export const reportMediaPopUp = (data) => dispatchAction(MEDIA_POP, data);
export const sharePostPopUP = (data) => dispatchAction(SHARE_POPUP, data);
export const sharePostClosePopUP = (data) => dispatchAction(SHARE_POPOF, data);
export const showPopUP = (data) => dispatchAction(POP_UP, data);
export const onSitePopup = (data) => dispatchAction(ONSITE_POPUP, data);
//export const hashtagCampaignImage = (data) => postRequest(DOMAIN_SCREENSHOT, data);

export const loadSiteFeed = () => {
    if (addfeedload && !messageSent) {
        window.parent.postMessage({ type: "reloadWebsite", data: JSON.stringify({ url: window.location.href }) }, "*");
        messageSent = true;
    }
};

const postRequest = (url, data) => new HttpClient().post(url, data);
export const sharePostByEmail = (data) => postRequest(SHARE_POST, data);
export const submitPostData = (data) => postRequest(ONSITE_UPLOAD, data);


export const onUpdateEditor = (updateData) => {
    try {
        return (dispatch) => {
            const { error, loader, WebFilter } = store.getState().appData
            
            const { after, Posts, wall, customPost, reviews, reviewFilters } = updateData;
            const isreviewFilters = Object.keys(reviewFilters || {}).length ? { reviewFilters } : [];

            const data = {
                after, Posts, 
                postData : Posts, wall, 
                Background : wall.Background, 
                Banner : wall.Banner, 
                CardStyle : wall.CardStyle, 
                CustomBanner : wall.CustomBanner,
               // Personalization : wall.Personalization,
                ProductSetting : wall.ProductSetting,
                SnapUp : wall.SnapUp ,
                ThemeInfo : wall.ThemeInfo,
                ThemeStyle : wall.ThemeStyle,
                Reviews : { ...wall.Reviews, ...reviews}, 
                planLimit: error.planLimit, 
                reviews: reviews, 
                reviewFilters : isreviewFilters,
                ...error.themeError, error_code: 0, customPost: customPost || [],  ...loader.loader, visitorLimit: 0,
                 response_code: 200, status: "success redis", webFilters: WebFilter
            };
            dispatch({ type: THEME_DATA, payload: data, heightEvent: null, themeId: "" });
        };
    }
    catch (ex) {
        console.log("onUpdateEditor", ex)
    }
}



