import HttpClient from "./http-client";
import SocialFonts from '../app/Components/Head/SocialFonts';
import {
    API_PATH, EMBED_TRACK, suiteTrack,
    isTagShop, isSocialWall, isEmbed, isSocialEmbed, TagShopEditor, isEditor
} from './api'

import { ERR_MSG, LOADER_ERR, THEME_DATA, ONSITE_TOKN, RENDER_ID, DELAY } from '../reducers/reducersKeys'
import { rID, urlDiscardMail, detectMobile, onsiteUpload } from '../utils';
import { getAttributes, getHashTagParam, getThemeId } from './commonAction';

//import { socialWallSocket } from './displayAction'

//let expirePostData = {};
export let dataSource = 'website';
export let dataRegion;
let messageSent = false;

export const themeByWallID = (Id, heightEvent) => {

    return (dispatch) => {
        try {
            SocialFonts();
            const isEmbedLoad = !isEditor;
            if (rID) dispatch({ type: RENDER_ID, payload: rID });

            const themeId = getThemeId(rID);
            let updateUrl = `${API_PATH}${Id.replace(/^\//, "")}${isEditor ? '/editor' : ''}`;

            if (isTagShop) {
                const { isStatus, mUrl } = getAttributes(rID)
                //console.log(isStatus, mUrl)
                if (isStatus) updateUrl = `${updateUrl}/${mUrl}`
            }

            // if (isTagShop && getAttributes(rID)?.isStatus)  updateUrl += `/${getAttributes(rID).mUrl}`;
            //if (isTagShop && getAttributes(rID)?.isStatus)  updateUrl += `${getAttributes(rID).mUrl}`;

            new HttpClient().get(updateUrl, getHashTagParam(rID, Id))
                .then((res) => {
                    const resdata = res.data.responseData;
                    const themeInfo = resdata?.ThemeInfo;
                    const getTheme = themeInfo?.id;
                    const posts = isTagShop ? resdata.Posts : res.data.Posts;
                    const mobCloumn = resdata.ThemeStyle.column.mobile
                    const slideDuration = resdata.ThemeStyle.slide.slideDuration

                    const postLimit = detectMobile() ? ![61, 62, 16, 55].includes(getTheme) &&
                        mobCloumn ? [4].includes(getTheme) ? posts.length : (mobCloumn * 2) :
                        [47, 55, 62, 4, 19, 3, 61, 16].includes(getTheme) ? 4 : [53].includes(getTheme) ? 15 : 5 :
                        [47].includes(getTheme) ? 10 : [61, 55, 62].includes(getTheme) ? 14 : [16].includes(getTheme) ? themeInfo.row ? 16 : 10 : [50].includes(getTheme) ? 21 : [53, 3, 4].includes(getTheme) ? 0 : 0;

                    const delayedPosts = postLimit ? posts.slice(postLimit) : [];

                    // const postLimit = []
                    // const delayedPosts = detectMobile() && ![53].includes(getTheme) ? posts.slice(postLimit) : [];

                    if (delayedPosts.length) posts.splice(postLimit);

                    const resData = isTagShop
                        ? { ...res.data, data: resdata }
                        : res;

                    const isResponse = suiteTrack || window.editor || resData.data.visitorLimit === 0 || isSocialWall || isSocialEmbed
                        ? resData.data.responseCode === 200
                        : resData.responseCode === 200;
                    if (isResponse) {
                        if (isEmbedLoad && !String(resData?.responseStatus || "").includes("redis")) urlTracking(Id, themeInfo.ownerId, 0);
                        dispatch({ type: THEME_DATA, payload: resData.data, heightEvent, themeId });
                        dispatch({ type: DELAY, payload: delayedPosts });

                        
                        if (onsiteUpload || isEmbed) dispatch({ type: ONSITE_TOKN, payload: resData.data.ONSITE_TOKN });
                        if (TagShopEditor && isEditor && !messageSent) {
                            window.parent.postMessage(
                                { type: "sreloadWebsite", data: JSON.stringify({ ...resData.data, url: window.location.href }) },
                                "*"
                            );
                            messageSent = true;
                        }
                    } else {
                        dispatch({
                            type: ERR_MSG,
                            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...resData.data },
                        });

                        if (TagShopEditor && window.location.href.includes("editor=1")) {
                            if (!messageSent) {
                                const newData = { url: window.location.href }
                                window.parent.postMessage({ type: "sreloadWebsite", data: JSON.stringify(newData) }, "*");
                                messageSent = true;
                            }
                        }
                    }
                })
                .catch(({ response }) => {
                    if (!isTagShop && (response?.status === 404 || (isSocialWall && response?.status === 401))) {
                        dispatch({ type: ERR_MSG, payload: response.data });
                        dispatch({ type: LOADER_ERR });
                    }
                });
        } catch ({ response }) {
            dispatch({
                type: ERR_MSG,
                payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response?.data },
            });
        }
    };
};


const urlTracking = (WALL_ID) => {

    try {
        const headers = {
            url: window.location.href,
            wall: WALL_ID,
            source: isTagShop ? 2 : 3,
            ...(isTagShop && dataSource?.includes("website") && { website: 1 }),
        };
        urlDiscardMail() && new HttpClient().post(`${EMBED_TRACK}`, {}, { headers });
    } catch (ex) {
        // Error handling logic can be added here if needed
    }
}



