import { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import './AppendView.scss'

const AppendedView = ({ children, wallId }) => {
    const [shouldRender, setShouldRender] = useState(false);

    useEffect(() => {
        if (wallId && children) setShouldRender(true);
        else setShouldRender(false);
    }, [wallId, children]);

    return shouldRender
        ? ReactDOM.createPortal(
            <div className="tb_subtree_container" data-widget-id={wallId}>
                {children}
            </div>,
            document.body
        )
        : <></>;
};

export default AppendedView;
