import axios from 'axios';
import { APIHOST, AUTH_TOKEN, isEditor, isUgcsuit } from './api';

const editor = isEditor ? { editor: 1 } : {};
const ugcSuite = window.ugcSuite || isUgcsuit ? { ugcSuite: 1 } : {};
const getdisplaywall = window.isSocialEmbed
    ? { getdisplaywall: 1, ispreview: 1 }
    : window.isSocial && !window.editor
    ? { ispreview: 1 }
    : {};

const requestInterceptor = (config) => ({
    ...config,
    headers: {
        'authorization': `Bearer ${window.location.href.includes("editor") && localStorage.getItem('token') ? localStorage.getItem('token') : AUTH_TOKEN}`,
        ...config.headers,
        productdomain: 'taggbox.com',
        requestDomain: window.location.href,
        isLiteCode: true,
        ...editor,
        ...ugcSuite,
        ...getdisplaywall,
    },
});

class HttpClient {
    constructor() {
        const instance = axios.create({ baseURL: APIHOST });
        instance.interceptors.request.use(requestInterceptor);
        instance.interceptors.response.use(
            (response) => response,
            (error) => Promise.reject(error)
        );
        return instance;
    }
}

export default HttpClient;
