import React, { Component } from 'react';
import { connect } from 'react-redux';
import { themeByWallID } from './actions/themeActions'
import { getWallID } from './utils'
import { addfeedload } from './actions/api'
import WidgetApp from './widgetApp'
import { loadSiteFeed } from './actions/OtherActions';
class Main extends Component {
    constructor(props) {
        super(props);
        let wallID = String(getWallID()).replace(/^0/, '');
        this.state = { wall_id: wallID };
        addfeedload ? loadSiteFeed() : props.themeByWallID(wallID, null);
      }

    render() {
        return <WidgetApp wallID={this.state.wall_id} />
    }
}


const mapDispatchToProps = dispatch => {
    return {
        themeByWallID: (Id, heightEvent) => dispatch(themeByWallID(Id, heightEvent))
    }
}
export default connect("", mapDispatchToProps)(Main);