export function getRootElements() {
    const taggbox_main = document.getElementById("taggbox_main");
    if (!taggbox_main) extendsGetRenderContainer();
    // Get the elements (return an array)
    return taggbox_main
        ? [taggbox_main]
        : Array.from(document.querySelectorAll(".tagshop-socialwall, .taggbox-socialwall"));
}


export const extendsGetRenderContainer = () => {
    try {
        var socialwall = document.getElementsByClassName(tagSocialWall).length
        var tagembed = document.getElementsByClassName(tagClass).length
        if (socialwall && tagembed) {
            if (document.getElementsByClassName(tagSocialWall)) {
                const tagembedSocial = document.getElementsByClassName(tagSocialWall)[0]
                if (tagembedSocial && tagembedSocial.getAttribute("data-render-id") && tagembedSocial.getAttribute("data-is-load") == "1") {
                    socialwall = 0
                }
            }
        }

        var container = socialwall && socialwall > 0 ? document.getElementsByClassName(tagSocialWall) : document.getElementsByClassName(tagClass);

        for (var i = 0; i < container.length; i++) {
            if (container[i].getAttribute("data-is-load") == "0") {
                if (container[i].getAttribute('data-render-id')) rID = container[i].getAttribute('data-render-id')
                return container[i].getAttribute("data-render-id")
            }
        }
    }
    catch (ex) {
        return document.getElementById('taggbox_main');
    }
}

export const getEmbed = () => {
    const isTagshop = document.getElementsByClassName("tagshop-socialwall")
    let containerElements = isTagshop?.length ? isTagshop : document.getElementsByClassName("taggbox-socialwall");
    return containerElements.length
}


export const getRenderContainer = () => {
    try {
        const isTagshop = document.getElementsByClassName("tagshop-socialwall")
        let containerElements = isTagshop?.length ? isTagshop : document.getElementsByClassName("taggbox-socialwall");

        if (containerElements.length) {

            for (let i = 0; i < containerElements.length; i++) {
                let element = containerElements[i];

                // 🔹 Skip already processed containers
                if (element.getAttribute("embed-is-load") === "1") {
                    continue;
                }

                // 🔹 Get the render container by `data-render-id`
                let renderId = element.getAttribute("data-render-id");
                if (renderId) {
                    rID = renderId
                    let targetContainer = document.getElementById(renderId);
                    if (targetContainer) {
                        // 🔹 Mark as processed
                        element.setAttribute("embed-is-load", "1");
                        return targetContainer; // ✅ Return only one unprocessed container
                    }
                }
            }

            return null; // ✅ No more containers left

        } else {
            return document.getElementById("taggbox_main"); // Default fallback
        }

    } catch (ex) {
        return document.getElementById("taggbox_main"); // Default fallback
    }
};


export let rID;

export const PostTrimContent = (str, length) => str?.length > length ? str.substring(0, length) + "..." : str;

export const urlDiscardMail = () => {
    try {
        const url = String(window.location.href);
        const keywords = [
            "googleusercontent",
            "inner-frame-minified",
            "localhost",
            "stage.",
            "beta.",
            `${Pwidget}.taggbox`,
            `${Pwidget}-${test}${tagembed}.com`,
            `${Pwidget}.${tagembed}.com`,
            `${Pwidget}s.${tagembed}.com`,
            // `${Pwidget}-${test}tagshop.ai`,
            // `${Pwidget}.tagshop.ai`,
            "127.0.0.1",
            `${Pdisplay}.taggbox`,
            `${Pdisplay}.socialwalls`,
            `${Pdisplay}-${test}socialwalls`
        ];

        return !(
            keywords.some((keyword) => url.includes(keyword)) ||
            window.preview ||
            window.editor
        );
    } catch {
        return true;
    }
};


export const getParameter = (n, t = window.location.href) => { n = n.replace(/[\[\]]/g, '\\$&'); const r = new RegExp('[?&]' + n + '(=([^&#]*)|&|#|$)'), e = r.exec(t); if (!e) return null; if (!e[2]) return ''; return decodeURIComponent(e[2].replace(/\+/g, ' ')); };

export const getCurrentTime = (isTime) => {
    const date = new Date();
    return [date.getMilliseconds(), date.getMinutes(), date.getHours(), date.getSeconds()][isTime - 1];
}


let postDetail = [];
let postId = [];
let listenerAdded = false;


const headers = new Headers({
    'Content-Type': 'application/json',
    'Authorization': `Bearer ${AUTH_TOKEN}`,
    'Productdomain': tbox
});

export const themeImpressionTracking = (entry) => {
    if (!isTagShop) {
        const { wall } = store.getState().appData;
        if (!listenerAdded && !isEditor) {
            window.addEventListener('beforeunload', () => {
                if (postDetail.length && ((isTagShop && !TagShopEditor) || (isTagbox && !window.editor))) {
                    fetch(UPDATE_TRACKING, {
                        method: 'POST',
                        headers,
                        body: JSON.stringify({
                            action: 2,
                            wall: wall.ThemeInfo[dataSource === 'website' ? 'webId' : 'wallId'],
                            owner: wall.ThemeInfo.owner,
                            model: dataSource === 'website' ? 'website' : 'wall',
                            posts: postDetail
                        }),
                        keepalive: true
                    });
                }
            });
            listenerAdded = true;
        }

        const postIdAttr = entry?.target?.getAttribute("post-id");
        if (postIdAttr && !postId.includes(postIdAttr)) {
            postDetail.push({ post: postIdAttr, feed: entry?.target?.getAttribute("feed-id") });
            postId.push(postIdAttr);
        }
    }
};
export const isInstaUser = (str) => str === 'Instagram User';

export const detectIphone = () => /(iphone)/i.test(navigator.userAgent);

export const detectMobile = () => /(iphone|android)/i.test(navigator.userAgent);

export const isMobile = () => /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

export const getQueryString = (url, parm) => new URL(url).searchParams.get(parm);


