export const APPEND_CHILD = (e, type = '', id = '', defer = false) => {
    const element = document.createElement(type === 'style' ? 'style' : type === 'script' ? 'script' : 'link');
    Object.assign(
        element,
        type === 'style'
            ? { type: 'text/css', id, innerHTML: e }
            : type === 'script'
                ? { src: e, defer }
                : { type: 'text/css', rel: 'stylesheet', href: e }
    );
    document.head.appendChild(element);
};

export const onsiteUpload = wUrl("onsite-upload")

export function wUrl(url, string = null) {
    return (string ? window.location.href.includes(url) : window.location.href.includes(url));
}
export const visitorRequest = () => {
    const { ThemeInfo } = store.getState().appData.wall || {};
    if (!urlDiscardMail() || isEditor) return;
    const data = { action: 3, wall: ThemeInfo?.webId, owner: ThemeInfo?.ownerId, ...(dataSource && { model: dataSource }) };
    new HttpClient().post(UPDATE_TRACKING, data).catch(() => { });
};
