import React, { Suspense, useEffect, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { connect } from 'react-redux';
import { APPEND_CHILD, detectMobile, getQueryString, onsiteUpload, wUrl } from './utils';
import { isTagShop, TagShopEditor, isSocialWall, isSocialEmbed, isEmbed, isEditor } from './actions/api';
import { onUpdateEditor } from './actions/OtherActions';
import { loadDelaydata, nextData } from './actions/moredataAction';
import AppendedView from './AppendedView'
const App = React.lazy(() => import('./app'));
const ErrorPage = React.lazy(() => import('./app/Components/ErrorPages'));
const AppHead = React.lazy(() => import('./app/Components/Head/Head'));
const UserComponents = React.lazy(() => import('./app/Components/UserComponents'));
const SharePost = React.lazy(() => import('./app/Components/UserComponents/SharePost'));
const PostPopUp = React.lazy(() => import('./app/Components/PostPopUp'));
const ReelPopUp = React.lazy(() => import('./app/Components/ReelPopUp'));
const ReportMedia = React.lazy(() => import('./app/Components/UserComponents/ReportMedia'));
const SocialWallBranding = React.lazy(() => import('./app/Components/CustomBranding/SocialWall'));
const UpgradePopUp = React.lazy(() => import('./app/Components/CustomBranding/UpgradePopUp'));
const Studio = React.lazy(() => import('./app/Components/Studio'));
const Fullscreen = React.lazy(() => import('./app/Components/Fullscreen'));
const Announcements = React.lazy(() => import('./app/Components/Announcements'));
const Spotlight = React.lazy(() => import('./app/Components/Spotlight'));

export const addScript = url => APPEND_CHILD(url, 'script', false, true)

const TaggboxThemes = ({ errorWithMessage, themeError, planLimit, ...props }) => {
  if (!errorWithMessage && (!planLimit || isTagShop) && (!themeError || !Object.keys(themeError).length || themeError.error_code === 0)) {
    return <Suspense fallback={<></>}><App {...props} /></Suspense>;
  } else if (!isTagShop && (errorWithMessage || planLimit || themeError)) {
    return <Suspense fallback={<></>}><ErrorPage {...props} /></Suspense>;
  }
  return null;
}

// const AppendedView = ({ children, wallId }) => {
//   return wallId ? <Portal id="subtree" className="tb_subtree">{wallId ? <div data-widget-id={wallId}>{children}</div> : null}</Portal> : null;
// }
const WidgetApp = ({ appData, modalPop, spotlight, wallID, onUpdateEditor, loadDelaydata }) => {
  const [errorData, setErrorData] = useState(null);
  const [render, setRender] = useState("");
  const [visibleBranding, setVisibleBranding] = useState(false);

  useEffect(() => {
    const { themeError } = appData.error || {};
    if (themeError?.error_code !== errorData?.error_code) {
      setVisibleBranding(true);
      setErrorData(themeError);
      if (window.onSetErrorCode && errorData) window.onSetErrorCode(errorData);
    }
    window.addEventListener?.("scroll", showBranding);
    return () => {
      window.removeEventListener("scroll", showBranding);
    };
  }, [appData.error, errorData]);

  const showBranding = () => setVisibleBranding(true);

  const onLoadEditor = () => {
    if (TagShopEditor) {
      try {
        window.addEventListener('message', handleMessage);
      } catch (error) {
        console.log("error", error);
      }
    } else if (wUrl("addfeedload=load")) {
      window.addEventListener('message', addFeedReloadTheme);
    }
  }

  useEffect(() => {
    onLoadEditor();
    return () => {
      const eventHandler = TagShopEditor ? handleMessage : addFeedReloadTheme;
      window.removeEventListener('message', eventHandler);
    };
  }, []);

  const handleMessage = (event) => {
    const { type, data } = event.data;
    if (type === "reloadWebsite") {
      const payload = JSON.parse(data);
      setRender(uuidv4());
      onUpdateEditor(payload);
    }
  }

  const loadMoredata = () => {
    loadDelaydata();
  }

  const addFeedReloadTheme = (e) => {
    const { type, data } = e.data;
    if (type === "reloadTheme") onUpdateEditor(data);
  }
  return (
    <>
      {isSocialWall && !onsiteUpload && appData?.isStudio === 1 && !getQueryString(window.location.href, 'studio') && !isEditor ? (
        <Studio wallID={appData?.wallID} />
      ) : (
        <>
          {isSocialWall && !isSocialEmbed ? <Suspense fallback={<></>}><Fullscreen /></Suspense> : null}
          {appData?.wall && Object.keys(appData.wall).length ? <Suspense fallback={<></>}><AppHead wall={appData?.wall} /></Suspense> : null}
          {modalPop.onSiteIsShowPopUp ? <Suspense fallback={<></>}> <AppendedView wallId={wallID}>
            <UserComponents modalPop={modalPop} appData={appData} /></AppendedView>
          </Suspense> : null}
          {appData?.wall && Object.keys(appData.wall).length ? <TaggboxThemes loadMoredata={loadMoredata} {...appData} key={render} keyRender={render} /> : null}
          {appData?.postData?.isFreeAdsStatus && isSocialWall ? (
            <AppendedView wallId={wallID}>
              <Suspense fallback={<></>}><SocialWallBranding appData={appData} /></Suspense>
              {!isEditor && isSocialWall ? <Suspense fallback={<></>}><UpgradePopUp appData={appData} /></Suspense> : ''}
            </AppendedView>
          ) : null}
          {modalPop.isShowPopUp ? (
            isTagShop && appData?.wall?.ThemeInfo?.ownerId !== 142546 && detectMobile() && ![64, 63].includes(appData?.wall?.ThemeInfo?.id) ? (
              <AppendedView wallId={wallID}>
                <Suspense fallback={<></>}>
                  <ReelPopUp wall={appData.wall} data={modalPop.data} networkData={appData.networkData} postData={appData.postData} brandingNetworks={appData?.webFilters?.map(({ networkId }) => networkId)} />
                </Suspense>
              </AppendedView>
            ) : (
              <AppendedView wallId={wallID}>
                <Suspense fallback={<></>}>
                  <PostPopUp wall={appData.wall} isShowPopUp={modalPop.isShowPopUp} data={modalPop.data} networkData={appData.networkData} postData={appData.postData} brandingNetworks={appData.webFilters?.map(({ networkId }) => networkId)} key={modalPop.isShowPopUp} />
                </Suspense>
              </AppendedView>
            )
          ) : null}
          {isSocialWall && appData.announcements ? <Suspense fallback={<></>}><Announcements announcements={appData.announcements} /></Suspense> : null}
          {modalPop.reportStatus && isEmbed ? (
            <AppendedView wallId={wallID}>
              <Suspense fallback={<></>}><ReportMedia item={modalPop.reportData} wall={appData.wall} /></Suspense>
            </AppendedView>
          ) : null}
          {modalPop.shareStatus && isEmbed ? (
            <AppendedView wallId={wallID}>
              <Suspense fallback={<></>}><SharePost postLink={modalPop.shareData.link} userName={modalPop.shareData.author.name} /></Suspense>
            </AppendedView>
          ) : null}
          {modalPop.reportStatus ? (
            <AppendedView wallId={wallID}>
              <Suspense fallback={<></>}><ReportMedia item={modalPop.reportData} wall={appData.wall} /></Suspense>
            </AppendedView>
          ) : null}
          {modalPop.shareStatus ? (
            <AppendedView wallId={wallID}>
              <Suspense fallback={<></>}><SharePost postLink={modalPop.shareData.link} userName={modalPop.shareData.author.name} /></Suspense>
            </AppendedView>
          ) : null}
          {isSocialWall && spotlight.status ? <Suspense fallback={<></>}><Spotlight item={spotlight.data} wall={appData.wall} /></Suspense> : null}
        </>
      )}
    </>
  );
}

const mapStateToProps = state => ({
  appData: state.appData,
  modalPop: state.modalPop,
  spotlight: state.spotlight
});

const mapDispatchToProps = {
  onUpdateEditor,
  nextData,
  loadDelaydata
};

export default connect(mapStateToProps, mapDispatchToProps)(WidgetApp);