import React from "react";
import { createRoot } from "react-dom/client";
import { Provider } from "react-redux";
import "./App.scss";
import store from "./store";
import Main from "./main"; 
import { getEmbed, getRenderContainer } from "./utils";

const rootsMap = new Map();

(function render() {
    const container = getRenderContainer();
    if (container) { 
        rootsMap.set(container, rootsMap.get(container) || createRoot(container));
        rootsMap.get(container).render(<Provider store={store}><Main /></Provider>);
        getEmbed() && setTimeout(render, 100); // Process next container
    }
})();

