import { MORE_LOADER, DATA_LOADER } from './reducersKeys'
const initialState = { isShowMoreLoading: false, isFilterLoading: false }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case MORE_LOADER: { return { ...state, isShowMoreLoading: action.payload } }
        case DATA_LOADER: { return { ...state, isFilterLoading: action.payload } }
        default:
            return { ...state }
    }

}